import React, { Suspense, lazy} from 'react'
import {Redirect, Switch} from "react-router-dom";
import Loading from "../../../components/shared-components/Loading";
import VillaRoute from "../../../permissions/VillaRoute";
const BookingViews = ({match})=> {
    return (
        <Suspense fallback={<Loading cover={'content'}/>}>
            <Switch>
                <VillaRoute path={`${match.url}/schedule`} component={lazy(()=> import("./schedule/index"))}/>
                <VillaRoute path={`${match.url}/log`} component={lazy(()=> import("./log-activity/index"))}/>
                <VillaRoute path={`${match.url}/p/:id`} component={lazy(()=> import("./details/index"))}/>
                {/*<Redirect to={`${match.url}/schedule`} from={`${match.url}`}/>*/}
            </Switch>
        </Suspense>

    )
}

export default BookingViews
